
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data(){
return {
refund_Info:"",
refund_id:"",
apply_desc: "",
isTui:false,
isxiugai:false,
nvabarData: {
showCapsule: 1, //是否显示左上角图标
title: '鞋品荟', //导航栏 中间的标题
},
}
},
created(){
this.refund_id = this.$route.query.refund_id;
this.showInfo();
},
methods: {
async showInfo(){
let data = {
plat: 3,
account: this.account,
token: this.token,
refund_id: this.refund_id
}
const [err, res] = await api.refunddetails(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
res.data.add_time = this.getLocalTime(res.data.add_time);
res.data.seller_time = this.getLocalTime(res.data.seller_time);
res.data.apply_desc = res.data.apply_desc.split("\\n");
this.refund_Info = res.data;
if (res.data.refund_state == "2") {
if (res.data.is_fill_in == 0) {
this.isTui = true;
} else {
this.isTui = false;
}
} else {
this.isTui = false;
}
if (res.data.refund_type != "3") {
if (res.data.refund_state == "1") {
this.isxiugai = false;
} else {
this.isxiugai = true;
}
} else {
if (res.data.refund_state == "1" || res.data.refund_state == "5") {
this.isxiugai = true;
} else {
this.isxiugai = false;
}
}
}
},
getLocalTime(nS){
var time = new Date(nS * 1000);
var date = (time.getFullYear()) + "-" +
(time.getMonth() + 1) + "-" +
(time.getDate()) + " " +
(time.getHours()) + ":" +
(time.getMinutes()) + ":" +
(time.getSeconds());
return date;
},
linkxieshang(){
this.$router.push({
path: '/centerFull/orderFull/aftersteps',
query: {
refund_id: this.refund_id
}
})
},
xiugai(){
this.$router.push({
path: '/centerFull/partner/aftersalerInfo',
query: {
refund_id: this.refund_id,
update: 1
}
})
},
chexiao(){
let that = this;
this.$vux.confirm.show({
title: '撤销申请',
content: '申请售后仅可撤销一次，您确定要撤销申请？',
cancelText:"暂不撤销",
confirmText:"撤销申请",
async onConfirm () {
let data = {
plat: 3,
account: that.account,
token: that.token,
refund_id: that.refund_id
}
const [err, res] = await api.refundrevoke(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
that.$vux.toast.text('撤销成功');
that.showInfo();
}
}
})
}
},
computed: {
...mapGetters(["user", "account", "token"])
}
}



